import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Home from "./Home";
import './styles.css';

function App() {
  return (
    <div>
      <a href="https://breloom.xyz/"><img src="./breloom.png" id="homebutton" /></a>
      <Router>
        <div>
          <Switch>
            <Route path="/">
              <Home />
            </Route>
            <Route exact path="/h" render={() => (window.location = "https://hahs.sentral.com.au/portal2/")} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;